import React, { PureComponent } from 'react';
import { withRouter } from 'react-router-dom';
import styled from 'styled-components';
import AppBar from '@material-ui/core/AppBar';
import Tabs from '@material-ui/core/Tabs';
import Tab from '@material-ui/core/Tab';

const Logo = styled(Tab)`
  opacity: 1;

  .MuiTab-wrapper {
    color: white !important;
    font-weight: bold;
    font-size: 1.1em;
  }
`;

class Nav extends PureComponent {
  getActiveTab = () => this.props.location.pathname.replace('/', '');

  handleChange = (e, path) => {
    e.preventDefault();
    this.props.history.push(`/${path}`);
  };

  render() {
    return (
      <AppBar position="static">
        <Tabs onChange={this.handleChange} value={this.getActiveTab()}>
          <Logo label="Broadcaster Editor POC" disabled />
          <Tab label="quill" value="quill" />
          <Tab label="editor js" value="editor-js" />
        </Tabs>
      </AppBar>
    );
  }
}

export default withRouter(Nav);
