import React, { PureComponent } from 'react';
import ReactQuill from 'react-quill';
import Grid from '@material-ui/core/Grid';

import { PocContainer, PocItem, PocHTMLOutput } from '../../Elements';

import 'react-quill/dist/quill.snow.css';

class Quill extends PureComponent {
  constructor(props) {
    super(props);
    this.state = {
      value: '',
      output: '',
    };
  }

  handleChange = (content, delta, source, editor) => {
    console.log('[POC]: content', content);
    console.log('[POC]: delta', delta);
    console.log('[POC]: source', source);
    console.log('[POC]: editor', editor, editor.getContents());
    if (source && editor) {
      const output = JSON.stringify(editor.getContents());
      this.setState({
        value: content,
        output,
      });
    }
  };

  render() {
    const { output, value } = this.state;
    return (
      <PocContainer>
        <h1>Quill</h1>

        <Grid container spacing={2}>
          <PocItem item>
            <h2>Editor</h2>
            <ReactQuill
              theme="snow"
              value={value}
              onChange={this.handleChange}
            />
          </PocItem>

          <PocItem item>
            <h2>JSON output</h2>
            <PocHTMLOutput>{output}</PocHTMLOutput>
          </PocItem>
        </Grid>
      </PocContainer>
    );
  }
}

export default Quill;
