import React, { PureComponent } from 'react';
import EditorJs from 'react-editor-js';
import Grid from '@material-ui/core/Grid';

import Embed from '@editorjs/embed';
import Table from '@editorjs/table';
import Paragraph from '@editorjs/paragraph';
import List from '@editorjs/list';
import Code from '@editorjs/code';
import LinkTool from '@editorjs/link';
import Image from '@editorjs/image';
import Raw from '@editorjs/raw';
import Header from '@editorjs/header';
import Quote from '@editorjs/quote';
import Marker from '@editorjs/marker';
import CheckList from '@editorjs/checklist';
import Delimiter from '@editorjs/delimiter';
import InlineCode from '@editorjs/inline-code';

import { PocContainer, PocItem, PocHTMLOutput } from '../../Elements';

export const EDITOR_JS_TOOLS = {
  embed: Embed,
  table: Table,
  paragraph: Paragraph,
  list: List,
  code: Code,
  linkTool: LinkTool,
  image: Image,
  raw: Raw,
  header: {
    class: Header,
    inlineToolbar: true,
  },
  quote: Quote,
  marker: Marker,
  checklist: CheckList,
  delimiter: Delimiter,
  inlineCode: InlineCode,
};

class Editor extends PureComponent {
  constructor(props) {
    super(props);
    this.state = {
      value: {},
      output: '',
    };
  }

  handleChange = (event, json) => {
    console.log('[POC]: event', event);
    console.log('[POC]: json', json);
    if (json && json.blocks) {
      const output = JSON.stringify(json.blocks);
      this.setState({ output });
    }
  };

  render() {
    const { value, output } = this.state;
    return (
      <PocContainer>
        <h1>Editor.js</h1>

        <Grid container spacing={2}>
          <PocItem item>
            <h2>Editor</h2>
            <EditorJs
              data={value}
              tools={EDITOR_JS_TOOLS}
              onChange={this.handleChange}
            />
            ;
          </PocItem>

          <PocItem item>
            <h2>JSON output</h2>
            <PocHTMLOutput>{output}</PocHTMLOutput>
          </PocItem>
        </Grid>
      </PocContainer>
    );
  }
}

export default Editor;
